.view-animate {
  transition: all .3s; }
  .view-animate.ng-enter, .view-animate.ng-leave {
    opacity: 1; }
  .view-animate.ng-enter, .view-animate.ng-leave-active, .view-animate.ng-hide {
    height: 0;
    opacity: 0; }

[class*='block-grid-'] {
  zoom: 1;
  display: block;
  padding: 0;
  margin: 0 -.55556rem; }
  [class*='block-grid-']:before, [class*='block-grid-']:after {
    content: '';
    display: table; }
  [class*='block-grid-']:after {
    clear: both; }
  [class*='block-grid-'] > li {
    display: block;
    height: auto;
    float: left;
    padding: 0 .55556rem 1.11111rem; }

.block-grid-6 > li {
  width: 16.66666%; }

.small-block-grid-3 > li {
  width: 33.33333%;
  list-style: none; }

.small-block-grid-3 > li:nth-of-type(1n) {
  clear: none; }

.small-block-grid-3 > li:nth-of-type(3n+1) {
  clear: both; }

.styleguide footer {
  display: none; }
.styleguide section {
  margin-top: 50px; }

.status.status-success {
  color: #3e53a4; }
.status.status-error {
  color: #dc0000; }
.status.status-warning {
  color: #bf6800; }

.preview-example {
  background-color: #fff;
  position: relative;
  padding: 40px 9px 9px;
  border: 1px solid #e3e3e3;
  border-radius: 3px;
  margin: 20px 0; }
  .preview-example:before {
    content: 'Example';
    position: absolute;
    font-size: 9px;
    top: 0;
    left: 0;
    padding: 3px 7px;
    text-transform: uppercase;
    background-color: #f5f5f5;
    border: 1px solid #e3e3e3;
    border-top: 0;
    border-left: 0;
    letter-spacing: .1em; }
  .preview-example.preview-example-live:before {
    font-family: 'FontAwesome';
    content: '\f0e7   Live example'; }
  .preview-example + .preview-code {
    margin-top: -25px;
    margin-bottom: 20px; }

.preview-code pre {
  border-color: #e3e3e3;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  margin: 0;
  padding: 10px; }
  .preview-code pre code {
    background: transparent; }

.panel-download .panel-body {
  min-height: 425px;
  transition: all .3s ease-in-out; }
  .panel-download .panel-body ul li {
    margin-bottom: 8px; }
  .panel-download .panel-body:hover {
    box-shadow: 0 11px 20px -5px rgba(0, 0, 0, 0.5);
    transition: all .3s ease-in-out; }
.panel-download a {
  color: #fff;
  font-weight: 200; }
  .panel-download a:hover {
    color: #fff; }
.panel-download hr {
  width: 50%; }
  .panel-download hr.web {
    border-color: #c68cc3; }
  .panel-download hr.ios {
    border-color: #717171; }
